@media (max-width: 720px) {
  .lr-c-navbar__links > .lr-c-navbar__item {
    display: block;
    width: 100%; } }

.lr-c-navbar__toggle {
  float: right;
  display: block;
  height: 70px;
  padding: 1rem;
  background-color: #343a40;
  cursor: pointer; }
  @media (min-width: 720px) {
    .lr-c-navbar__toggle {
      display: none; } }

.lr-c-navbar__overlay {
  position: fixed;
  top: 70px;
  bottom: 0;
  left: 0;
  right: 0;
  height: 0;
  width: 100%;
  background-color: #343a40;
  opacity: 0.8;
  transition: all 0.3s ease-in; }

@media (max-width: 720px) {
  .lr-c-navbar.is-open .lr-c-navbar__overlay {
